* {
  margin: 0;
  padding: 0;
}

.App {
  font-family: sans-serif;
  min-height: 100%;
  height: auto !important;
  position: relative;
  box-sizing: border-box;
  padding-bottom: 100px;
}

section{
  margin: 60px 0;
  width: 100%;
  box-sizing: border-box;
  padding: 0 24px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1, h2 {
  margin: 25px 0;
  text-align: center;
  width: 100%;
}

button {
  width: fit-content !important;
  margin: 10px 15px 10px 0 !important;
}

nav ul {
  display: flex;
  list-style: none;
}

li {
  margin: 0 5px;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  padding: 0 12px;
  filter: brightness(.9);

  &:hover {
    filter: brightness(1);
  }
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 600px;
}

.input {
  width: 100% !important;
  margin-bottom: 20px !important;
}